.App-logo {
    height: 28px;
    margin-left: 20px;
    margin-bottom: 10px;
 }
 
 
 .App-header {
    display: flex;
    align-items: center;
    height: 70px;
    width: 100vw;
    color: #fff;
    background-color: #00305C;
    box-shadow: rgb(220, 220, 220) 0px 2px 10px;
 }
 
 .title {
    font-size: 20px;
    font-weight: bold;
    margin: 0px;
 }
 
.App {
  text-align: center;
  min-height: 100vh;
  overflow: hidden;
  display: block;
  position: relative;
  padding-bottom: 5px;
 }
 
.footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 100%;
    text-align: center;
}

.left-side {
    padding: 20px;
}

.right-side {
    height: 760px;
}

.sat-setting {
    width: 100%;
    background-color: #fbfbfb;
    border: 1px solid #d9d9d9;
    padding: 10px 10px 0px 5px;
    text-align: left;
}

.show-nearby {
    display: flex;
    justify-content: center;
}

.sat-list-box {
   border: 2px solid #ccc;
   height: 380px;
   margin-top: 10px;
   padding: 10px;
}

.btn-container {
   text-align: center;
}

.spin-box {
   display: flex;
   align-items: center;
   justify-content: center;
   height: 320px;
}

.sat-list {
   height: 310px;
   overflow: scroll;
}

.map-box {
    position: relative;
    height: 100%;
    width: 100%;
    Overflow: scroll;
}

.map, .track, .spinner {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}

.map {
    z-index: 0;
}

.track {
    z-index: 100;
}

.spinner {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 600px;
    width: 960px;
}

.hint {
    color: red;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    width: 100%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
     'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}


